import { useState } from "react";
import styled from "styled-components";
import { Title2, Title3 } from "../../assets/styles/component";
import CasesInner from "./Cases/CasesInnerMobile";
import PartnersInner from "./Partners/PartnersInnerMobile";

const SPartners = styled.section`
  padding: 3.75rem 0;

  background: radial-gradient(
      105.94% 105.94% at 113.83% -33.36%,
      #ff7cda 0%,
      rgba(255, 124, 218, 0) 100%
    ),
    radial-gradient(
      45.01% 95.17% at 134.48% 0%,
      #7400e8 0%,
      rgba(116, 0, 232, 0) 100%
    ),
    radial-gradient(
      40.65% 44.52% at 90.65% 94.48%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      54.92% 55.79% at 45.91% 41.36%,
      rgba(0, 174, 232, 0.91) 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    radial-gradient(
      56.31% 80.35% at 14.82% 95.16%,
      #7400e8 0%,
      rgba(116, 0, 232, 0) 100%
    ),
    radial-gradient(
      47.68% 95.44% at -2.01% -21.74%,
      #00aee8 0%,
      rgba(0, 174, 232, 0) 100%
    ),
    #000f67;

  & > div:first-child {
    margin-bottom: 0.625rem;
  }
`;

const Partners = ({ Logos, language }) => {
  return (
    <SPartners>
      <PartnersInner Logos={Logos} language={language} />
      <CasesInner language={language} />
    </SPartners>
  );
};

export default Partners;
