import styled from "styled-components";
import { css } from "styled-components";
import { EngLoc } from "../../../assets/constants/localizations";
import { Text, Title3 } from "../../../assets/styles/component";
import Button from "../../../assets/UI/ButtonMobile";

const SCase = styled.div`
  position: absolute;

  display: flex;
  flex-direction: column;
  justify-content: space-between;

  width: 100%;
  max-width: 31.25rem;
  height: 22.375rem;

  padding: 1.563rem;
  box-sizing: border-box;

  text-align: left;
  border: 2px solid #ffffff2a;
  border-radius: 1.063rem;

  transform: translateX(calc(105% + ${({ index = 0 }) => +index * 5}%));
  z-index: ${({ index = 0 }) => index + 100};
  transition: 0.8s ease-out;

  & p,
  & h3,
  & button {
    opacity: 0.4;
  }

  &::after {
    top: 0;
    left: 0;
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 1.063rem;
    background: linear-gradient(261.19deg, #522ec1 12.71%, #1087c8 97.24%);
    opacity: 0.7;
    transition: 1s;
  }

  ${({ active = false }) =>
    active &&
    css`
      pointer-events: all;
      transform: translateX(0);

      opacity: 1;

      &::after {
        opacity: 0;
      }

      & p,
      & h3,
      & button {
        opacity: 1;
      }
    `}

  & > *:not(:last-child) {
    z-index: ${({ index = 0 }) => index + 100};
  }

  & button {
    width: fit-content;
    border: 1px solid #fff;
    background: none;
    border-radius: 3.125rem;
    padding: 1.25rem 3.188rem;

    font-weight: 400;
    font-size: 1rem;
    line-height: 1.063rem;
    letter-spacing: 0.02em;
    background-color: #fff;
    color: #00aee8;
  }
`;

const Title = styled(Title3)`
  font-weight: 400;
  font-size: 1.875rem;
  line-height: 130%;
`;

const Description = styled(Text)`
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 150%;
  max-width: 52.5rem;
`;

const Categories = styled.div`
  display: flex;
  gap: 1.813rem;
  margin: 1.625rem 0;
`;

const Category = styled.div``;

const TitleValue = styled(Text)`
  font-weight: 400;
  font-size: 0.75rem;
  line-height: 150%;
`;

const Value = styled(Text)`
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 150%;
`;

const Case = ({
  index,
  active,
  title,
  setActive,
  desc,
  design,
  value1,
  value2,
  value3,
  valueTitle1,
  valueTitle2,
  valueTitle3,
  language,
}) => {
  return (
    <SCase onClick={() => {setActive(index)}} index={index} active={active}>
      <Title>{title}</Title>
      <Description>{desc}</Description>
      <Categories>
        {value1 && (
          <Category>
            <TitleValue color="#FFFFFF80">
              {valueTitle1
                ? valueTitle1
                : language === "Ru"
                ? "Кампания"
                : EngLoc.partners.cases.period}
            </TitleValue>
            <Value size={36} weight={600}>
              {value1}
            </Value>
          </Category>
        )}
        {value2 && (
          <Category>
            <TitleValue color="#FFFFFF80">
              {valueTitle2
                ? valueTitle2
                : language === "Ru"
                ? "Подписчиков"
                : EngLoc.partners.cases.subscribers}
            </TitleValue>
            <Value size={36} weight={600}>
              {value2}
            </Value>
          </Category>
        )}
      </Categories>
      {/* <Button colorTxt="#00aee8" type="secondary" link={true}>
        {language === "Ru" ? "Подробнее" : EngLoc.buttons.more}
      </Button> */}
      {design}
    </SCase>
  );
};

export default Case;
