import { forwardRef, useEffect, useRef, useState } from "react";
import styled from "styled-components";

import { EngLoc } from "../../../assets/constants/localizations";

import { Title2 } from "../../../assets/styles/component";
import Toggler from "../TogglerMobile";

const SPartnersInner = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3.75rem;
  margin: 0 1rem;

  @media (min-width: 600px) {
    margin: 0 4%;
  }
`;

const LogosBox = styled.div`
  display: flex;
  visibility: ${({ active = false }) => (active ? "visible" : "hidden")};
  align-items: center;
  justify-content: center;

  width: calc(25% - 0.625rem);
  max-width: 100px;
  height: 5.125rem;
  background: rgba(223, 236, 253, 0.15);
  border: 1px solid rgba(241, 249, 255, 0.25);
  border-radius: 0.5rem;
  position: relative;

  & svg {
    max-width: 2.625rem;
    max-height: 2.188rem;
  }

  opacity: 0;
  transition: ${({ timer = 0.5 }) => timer}s ease-in-out;
  ${({ active = false }) => (active ? `opacity: 1;` : `opacity: 0;`)}

  &::before {
    position: absolute;
    content: "";
    top: -2px;
    left: 0;
    width: calc(50%);
    height: 5.125rem;
    background-color: white;
    border: 2px solid rgba(241, 249, 255, 0.25);
    border-radius: 0.5rem;
    z-index: 1;

    transition: 0.5s ease-in-out;
    opacity: 0;
  }

  &::after {
    position: absolute;
    content: "";
    top: -2px;
    left: 0;
    width: calc(50%);
    height: 5.125rem;
    z-index: 2;
    background: ${({ img }) => "url(" + img + ")"};
    transition: 0.5s ease-in-out;
    opacity: 0;
    background-repeat: no-repeat;
    background-size: 30% auto;
    background-position: center;
    padding-left: 235%;
  }

  &:hover {
    svg {
      z-index: 3;
      path {
        fill: #0079e6;
      }
    }

    &::after,
    &::before {
      opacity: 1;

      &:nth-child(4n) {
        transform: translateX(-5.8rem);
      }
    }

    &::before {
      width: calc(200% + 0.6rem);
      height: 5.125rem;
    }
  }

  @media(max-width: 575px){
    &:nth-child(4n) {
      &:hover::after {
        margin-left: 15px;
        padding-left: 0;
        padding-right: 258%;
        background-position: left;
      }
  
      &:hover::before {
        transform: translateX(calc(-50% - 0.4rem));
      }
      &:hover::after { 
        transform: translateX(-35%);
      }
    }
  }

  @media (max-width: 710px) and (min-width: 576px){
    &:nth-child(5n) {
      &:hover::after {
        margin-left: 15px;
        padding-left: 0;
        padding-right: 258%;
        background-position: left;
      }
  
      &:hover::before {
        transform: translateX(calc(-50% - 0.4rem));
      }
      &:hover::after { 
        transform: translateX(-35%);
      }
    }
  }
  
  @media(max-width: 830px) and (min-width: 711px){
    &:nth-child(6n) {
      &:hover::after {
        margin-left: 15px;
        padding-left: 0;
        padding-right: 258%;
        background-position: left;
      }
  
      &:hover::before {
        transform: translateX(calc(-50% - 0.4rem));
      }
      &:hover::after { 
        transform: translateX(-35%);
      }
    }
  }

  @media(max-width: 951px) and (min-width: 831px){
    &:nth-child(7n) {
      &:hover::after {
        margin-left: 15px;
        padding-left: 0;
        padding-right: 258%;
        background-position: left;
      }
  
      &:hover::before {
        transform: translateX(calc(-50% - 0.4rem));
      }
      &:hover::after { 
        transform: translateX(-35%);
      }
    }
  }

  @media(max-width: 990px) and (min-width: 952px){
    &:nth-child(8n) {
      &:hover::after {
        margin-left: 15px;
        padding-left: 0;
        padding-right: 258%;
        background-position: left;
      }
  
      &:hover::before {
        transform: translateX(calc(-50% - 0.4rem));
      }
      &:hover::after { 
        transform: translateX(-35%);
      }
    }
  }
`;

const GridLogos = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  gap: 0.625rem;
`;

const Title = styled(Title2)`
  width: 100%;
  font-size: 2.375rem;
  font-weight: 400;
  text-align: left;
`;

const PartnersInner = ({ Logos, language }) => {
  const [view, setView] = useState(true);

  const intersectionObserver = new IntersectionObserver((entries) => {
    if (entries[0].intersectionRatio <= 0) return;
    setView(false);
  });

  useEffect(() => {
    intersectionObserver.observe(elem.current && elem.current);
  }, []);

  const elem = useRef(null);

  const [typeBrand, setTypeBrand] = useState(0);

  return (
    <SPartnersInner ref={elem} view={view}>
      <Title>
        {language === "Ru" ? "Клиенты" : EngLoc.partners.partners_slider.title}
      </Title>
      <Toggler handler={setTypeBrand} language={language}></Toggler>
      <GridLogos>
        {Logos.map((logo, index) => (
          <LogosBox
            key={index}
            type={logo.type}
            active={typeBrand === logo.type || typeBrand === 0}
            img={logo.text_img}
          >
            {logo.img}
          </LogosBox>
        ))}
      </GridLogos>
    </SPartnersInner>
  );
};

export default PartnersInner;
