import { useEffect } from "react";
import { useState } from "react";

import styled from "styled-components";
import { css } from "styled-components";
import { EngLoc } from "../../../assets/constants/localizations";

import Button from "../../../assets/UI/ButtonMobile";
import Case from "./CaseMobile";

import arrow from "../../../assets/images/Elements/Arrow.svg";
import SberDesignSvg from "../../../assets/images/Designs/SberDesignSvg.svg";
import SKSkolkovoDesignSvg from "../../../assets/images/Designs/SKSkolkovoDesignSvg.svg";
import WallStreetDesignSvg from "../../../assets/images/Designs/WallStreetDesignSvg.svg";
import GlamourDesignSvg from "../../../assets/images/Designs/GlamourDesignSvg.svg";
import BKSDesignSvg from "../../../assets/images/Designs/BKSDesignSvg.svg";
import OKKODesignSvg from "../../../assets/images/Designs/OKKODesignSvg.svg";

const SSlider = styled.div`
  display: flex;
  flex-direction: column;
  gap: 3.125rem;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;

  & > img {
    opacity: 0.6;
    transition: 0.25s;
    user-select: none;
  }

  & > img:hover {
    opacity: 1;
  }
`;

const SliderBox = styled.div`
  position: relative;
  width: 100%;
  min-height: 20.625rem;
`;

const SliderNav = styled.div`
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;

  & > div {
    display: flex;
    align-items: center;
    gap: 3.563rem;
  }

  & button {
    border: 1px solid #fff;
    background: none;
    border-radius: 3.125rem;
    padding: 1.25rem 3.125rem;

    font-weight: 400;
    font-size: 0.875rem;
    line-height: 1.063rem;
    letter-spacing: 0.02em;
    color: #ffffff;

    transition: 0.4s ease-in-out;

    :hover {
      background-color: #fff;
      color: #00aee8;
    }
  }
`;

const Arrow = css`
  height: 1.375rem;
  width: 0.75rem;
  cursor: pointer;
  opacity: 0.4;
  transition: 0.4s ease-in-out;
  &:hover {
    opacity: 1;
  }
`;

const ArrowLeft = styled.img`
  ${Arrow}
  transform: rotate(180deg);
`;

const ArrowRight = styled.img`
  ${Arrow}
`;

const Design = css`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 0.938rem;
  z-index: 0;
  overflow: hidden;

  & > img {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
  }
`;

const Background = styled.div`
  ${Design}
  ${({ design }) => design};
`;

const CasesBox = styled.div`
  display: flex;
  width: 100%;
  gap: 1.25rem;

  position: relative;
`;

const Slider = ({ language }) => {
  const [active, setActive] = useState(0);

  const Cases = [
    {
      title: "БКС Премьер",
      desc:
        language === "Ru"
          ? `БКС презентовал свои услуги и возможность инвестирования в продукты брокера для умножения своих средств`
          : EngLoc.partners.cases.cases_list[0],
      value1: "8 " + (language === "Ru" ? "Дней" : "Days"),
      value2: "316к",
      value3: "62к",
      design: (
        <Background
          design={css`
            background: linear-gradient(
              243.39deg,
              #1b6fff 4.1%,
              #6ba8ff 142.69%
            );
          `}
        >
          <img loading="lazy" src={BKSDesignSvg} alt="BKS" />
        </Background>
      ),
    },
    {
      title: "OKKO",
      desc:
        language === "Ru"
          ? `Реклама челленджа для киноманов от онлайн-кинотеатра Окко`
          : EngLoc.partners.cases.cases_list[1],
      value1: "11 " + (language === "Ru" ? "Дней" : "Days"),
      valueTitle2: language === "Ru" ? "Охват" : EngLoc.partners.cases.coverage,
      value2: "433к",
      design: (
        <Background
          design={css`
            background: linear-gradient(
              243.39deg,
              #210133 4.1%,
              #380a4b 142.69%
            );
          `}
        >
          <img loading="lazy" src={OKKODesignSvg} alt="OKKO" />
        </Background>
      ),
    },
    {
      title: "SK Сколково",
      desc:
        language === "Ru"
          ? `Рекламная кампания для Бизнес-школы Сколково в Телеграм -
  продвижение мастер-класса от Тома Роулинса. Как результат:
  sold-out`
          : EngLoc.partners.cases.cases_list[2],
      value1: "1 " + (language === "Ru" ? "Месяц" : "Month"),
      value2: "316к",
      value3: "79к",
      design: (
        <Background
          design={css`
            background: linear-gradient(
              243.39deg,
              #593ff5 4.1%,
              #9a7ffc 142.69%
            );
            & > img {
              bottom: -1.875rem;
            }
          `}
        >
          <img loading="lazy" src={SKSkolkovoDesignSvg} alt="SKSkolkovo" />
        </Background>
      ),
    },
    {
      title: "Glamour",
      desc:
        language === "Ru"
          ? `Размещения в Телеграм для Glamour - тестирование нового инструмента для крупного глянцевого издания`
          : EngLoc.partners.cases.cases_list[3],
      value1: "1 " + (language === "Ru" ? "Месяц" : "Month"),
      value2: "5к",
      value3: "183к",
      design: (
        <Background
          design={css`
            background: linear-gradient(
              243.39deg,
              #e71c8d 4.1%,
              #ffb6e0 142.69%
            );
          `}
        >
          <img loading="lazy" src={GlamourDesignSvg} alt="Glamour" />
        </Background>
      ),
    },
    {
      title: "Wall Street English",
      desc:
        language === "Ru"
          ? `Реклама школы английского языка на жителей Москвы с помощью посевов на всех площадках SocialJet`
          : EngLoc.partners.cases.cases_list[4],
      value1: "5 " + (language === "Ru" ? "Месяцев" : "Months"),
      value2: "2к",
      value3: "4,7 " + (language === "Ru" ? "Млн" : "Million"),
      design: (
        <Background
          design={css`
            background: linear-gradient(
              243.39deg,
              #e03d46 4.1%,
              #023260 86.87%
            );
          `}
        >
          <img loading="lazy" src={WallStreetDesignSvg} alt="WallStreet" />
        </Background>
      ),
    },
    {
      title: "Сбербанк",
      desc:
        language === "Ru"
          ? `Сбербанк анонсирует запуск проекта инвестирования 500 лучших стартапов`
          : EngLoc.partners.cases.cases_list[5],
      value1: "12 " + (language === "Ru" ? "Дней" : "Days"),
      value2: "898к",
      value3: "216к",
      design: (
        <Background
          design={css`
            background: linear-gradient(
              243.39deg,
              #17b82e 4.1%,
              #8fff36 142.69%
            );
          `}
        >
          <img loading="lazy" src={SberDesignSvg} alt="Sber" />
        </Background>
      ),
    },
  ];

  useEffect(() => {
    if (active > 5) setActive(0);
    if (active < 0) setActive(5);
  }, [active]);

  const [touchPosition, setTouchPosition] = useState(null);

  const handleTouchStart = (e) => {
    const touchDown = e.touches[0].clientX;
    setTouchPosition(touchDown);
  };

  const handleTouchMove = (e) => {
    const touchDown = touchPosition

    if(touchDown === null) {
        return
    }

    const currentTouch = e.touches[0].clientX
    const diff = touchDown - currentTouch

    if (diff > 12) {
        setActive(active + 1);
    }

    if (diff < -12) {
        setActive(active - 1);
    }

    setTouchPosition(null)
}

  return (
    <SSlider>
      <SliderBox>
        <CasesBox onTouchStart={handleTouchStart} onTouchMove={handleTouchMove}>
          <Case
            index={0}
            active={0 === active}
            setActive={setActive}
            language={language}
            title={Cases[0].title}
            desc={Cases[0].desc}
            value1={Cases[0].value1}
            value2={Cases[0].value2}
            value3={Cases[0].value3}
            valueTitle1={Cases[0].valueTitle1}
            valueTitle2={Cases[0].valueTitle2}
            valueTitle3={Cases[0].valueTitle3}
            design={Cases[0].design}
          />
          <Case
            index={1}
            active={1 === active}
            language={language}
            setActive={setActive}
            title={Cases[1].title}
            desc={Cases[1].desc}
            value1={Cases[1].value1}
            value2={Cases[1].value2}
            value3={Cases[1].value3}
            valueTitle1={Cases[1].valueTitle1}
            valueTitle2={Cases[1].valueTitle2}
            valueTitle3={Cases[1].valueTitle3}
            design={Cases[1].design}
          />
          <Case
            index={2}
            active={2 === active}
            language={language}
            setActive={setActive}
            title={Cases[2].title}
            desc={Cases[2].desc}
            value1={Cases[2].value1}
            value2={Cases[2].value2}
            value3={Cases[2].value3}
            valueTitle1={Cases[2].valueTitle1}
            valueTitle2={Cases[2].valueTitle2}
            valueTitle3={Cases[2].valueTitle3}
            design={Cases[2].design}
          />
          <Case
            index={3}
            active={3 === active}
            language={language}
            setActive={setActive}
            title={Cases[3].title}
            desc={Cases[3].desc}
            value1={Cases[3].value1}
            value2={Cases[3].value2}
            value3={Cases[3].value3}
            valueTitle1={Cases[3].valueTitle1}
            valueTitle2={Cases[3].valueTitle2}
            valueTitle3={Cases[3].valueTitle3}
            design={Cases[3].design}
          />
          <Case
            index={4}
            active={4 === active}
            language={language}
            setActive={setActive}
            title={Cases[4].title}
            desc={Cases[4].desc}
            value1={Cases[4].value1}
            value2={Cases[4].value2}
            value3={Cases[4].value3}
            valueTitle1={Cases[4].valueTitle1}
            valueTitle2={Cases[4].valueTitle2}
            valueTitle3={Cases[4].valueTitle3}
            design={Cases[4].design}
          />
          <Case
            index={5}
            active={5 === active}
            language={language}
            setActive={setActive}
            title={Cases[5].title}
            desc={Cases[5].desc}
            value1={Cases[5].value1}
            value2={Cases[5].value2}
            value3={Cases[5].value3}
            valueTitle1={Cases[5].valueTitle1}
            valueTitle2={Cases[5].valueTitle2}
            valueTitle3={Cases[5].valueTitle3}
            design={Cases[5].design}
          />
        </CasesBox>
      </SliderBox>
      <SliderNav>
        <Button>Все кейсы</Button>
        <div>
          <ArrowLeft
            loading="lazy"
            src={arrow}
            width="17px"
            height="30px"
            alt="arrowLeft"
            onClick={() => setActive(active - 1)}
          />
          <ArrowRight
            loading="lazy"
            width="17px"
            height="30px"
            src={arrow}
            alt="arrowRight"
            onClick={() => setActive(active + 1)}
          />
        </div>
      </SliderNav>
    </SSlider>
  );
};

export default Slider;
