import { useEffect } from "react";
import { useState } from "react";
import styled from "styled-components";

import { EngLoc } from "../../assets/constants/localizations";

const SToggler = styled.div`
  display: flex;
  justify-content: flex-start;
  width: 100%;
  gap: 0.313rem;
`;

const Variant = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;

  padding: 0.438rem 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.25);
  border-radius: 2.156rem;

  font-weight: 400;
  font-size: 0.875rem;
  line-height: 1.063rem;
  color: #ffffff;

  background: transparent;
  cursor: pointer;

  ${({ active }) => active && `background: #fff; color: #0B85DA;`}

  transition: .25s ease-in-out;
`;

const Toggler = ({ handler, language }) => {
  const [isActive, setIsActive] = useState(0);

  useEffect(() => {
    handler(isActive);
  }, [isActive]);
  return (
    <SToggler>
      <Variant active={isActive == 0} onClick={() => setIsActive(0)}>
        {language === "Ru" ? "Все" : EngLoc.partners.partners_slider.toggler[0]}
      </Variant>
      <Variant active={isActive == 1} onClick={() => setIsActive(1)}>
        {language === "Ru"
          ? "Рекламные агентства"
          : EngLoc.partners.partners_slider.toggler[1]}
      </Variant>
      <Variant active={isActive == 2} onClick={() => setIsActive(2)}>
        {language === "Ru"
          ? "Бренды"
          : EngLoc.partners.partners_slider.toggler[1]}
      </Variant>
    </SToggler>
  );
};

export default Toggler;
